@import url(https://fonts.googleapis.com/css?family=Yeon+Sung);
#app {
  /* font-family: 'Avenir', Helvetica, Arial, sans-serif; */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
/* body {
  background: url('./assets/background.jpg') no-repeat center center fixed;
} */
input:focus, textarea:focus, select:focus {
    outline: none;
}
#dimigo-logo {
    width: 120px;
    height: auto;
}
.link {
    text-decoration: none;
}
.wordbooks {
    text-align: center;
}
.wordbook {
    display: inline-block;
    width: 75%;
    padding: 3px;
    margin-bottom: 10px;
    background: none;
    border: 7px solid transparent;
    -o-border-image: linear-gradient(to left, #EC008C, #FC6767);
       border-image: -webkit-gradient(linear, right top, left top, from(#EC008C), to(#FC6767));
       border-image: linear-gradient(to left, #EC008C, #FC6767);
    border-image-slice: 1;
}
.wordbook .header {
    margin-top: 5px;
}
.wordbook .title {
    color: #EC008C;
    background: -webkit-linear-gradient(135deg, #EC008C, #FC6767);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 32px;
    text-decoration: none;
    margin-top: 5px;
}
.wordbook .intro {
    color: black;
    margin-top: 5px;
    margin-bottom: 5px;
}
.words {
    margin-bottom: 5px;
}
.word {
    display: inline-block;
    color: #EC008C;
    font-weight: 450;
    border: 2px solid #EC008C;
    border-radius: 15px;
    padding: 3.5px;
    margin-bottom: 5px;
    text-decoration: none;
}
.word:hover {
    color: white;
    background: #EC008C;
    background: -webkit-gradient(linear, left top, right top, from(#EC008C), to(#FC6767));
    background: linear-gradient(to right, #EC008C, #FC6767);
}
.word.gradient {
    color: white;
    background: #EC008C;
    background: -webkit-gradient(linear, left top, right top, from(#EC008C), to(#FC6767));
    background: linear-gradient(to right, #EC008C, #FC6767);
    margin: 0;
    margin-left: 5px;
}
.fa-ellipsis-h {
    color: rgb(223, 223, 223);
}
#guess {
    width: 50%;
    font-size: 50px;
    text-align: center;
    border: 5px rgb(37, 37, 37) dotted;
    border-radius: 0.4rem;
}
#progress {
    display: inline-block;
    width: 80%;
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: rgb(165, 165, 165);
}
#time {
    color: white;
    font-weight: 200;
    font-family: 'Yeon Sung', cursive;
    display:table-cell;
    vertical-align:middle;
    padding: 7px;
}
#bar {
    display:table;
    width: 1%;
    height: 30px;
    background-color: #EC008C;
    background: -webkit-gradient(linear, right top, left top, from(#EC008C), to(#FC6767));
    background: linear-gradient(to left, #EC008C, #FC6767);
    text-align: right;
}
#question {
    color: black;
    font-size: 50px;
    font-weight: 200;
    font-family: 'Yeon Sung', cursive;
}
.fa-redo {
    width: 50px;
    margin: 5px;
    color: rgb(37, 37, 37);
}
.fa-bars {
    width: 50px;
    margin: 5px;
    color: rgb(37, 37, 37);
}
.new-wordbook {
    display: inline-block;
    width: 30%;
    color: black;
    margin-top: 25px;
    margin-bottom: 25px;
    border: 3px rgb(37, 37, 37) solid;
    border-radius: 25px;
    font-size: 25px;
    font-family: 'Yeon Sung', cursive;
}
.new-wordbook:hover {
    color: white;
    background: black;
}
.word-table {
    display: inline-block;
    border-collapse: collapse;
}
table td {
    padding: 10px;
}
table tr td:last-child {
    border-left: 2px solid #EC008C;
}
input[type=checkbox]{
    transform: scale(1.5);
    -ms-transform: scale(1.5);
    -moz-transform: scale(1.5);
    -webkit-transform: scale(1.5);
    -o-transform: scale(1.5);
    padding: 10px;
}
.new-help {
    font-size: 24px;
    font-family: 'Yeon Sung', cursive;
}
.new-word {
    display: inline-block;
    color: #EC008C;
    border: 3px #EC008C solid;
    border-radius: 25px;
    font-size: 22px;
    font-family: 'Yeon Sung', cursive;
    margin-bottom: 30px;
    margin-top: 20px;
}
.new-word:hover {
    color: white;
    background: #EC008C;
}
.word-input {
    font-size: 23px;
}
.word-input span {
    font-family: 'Yeon Sung', cursive;
}
.no-words {
    display: inline-block;
    font-size: 20px;
    font-family: 'Yeon Sung', cursive;
    color: #EC008C;
    border: 2px #EC008C solid;
    background: white;
    border-radius: 25px;
}
.no-words:hover {
    color: white;
    background: #EC008C;
}
.add-words-box {
    text-align: center;
}
.english-input {
    color: black;
    border: 3px rgb(37, 37, 37) dotted;
    border-radius: 0.4rem;
}
.korean-input {
    color: black;
    border: 2px rgb(165, 165, 165) solid;
    border-radius: 10px;
    font-size: 20px;
    margin-top: 10px;
}
.add-korean {
    display: inline-block;
    font-size: 20px;
    font-family: 'Yeon Sung', cursive;
    color: #FC6767;
    border: 2px #FC6767 solid;
    background: white;
    border-radius: 25px;
    padding-left: 5px;
    padding-right: 5px;
    margin-top: 10px;
}
.add-korean:hover {
    color: white;
    background: #FC6767;
}
.wordbook-info {
    width: 100%;
}
.wordbook-info input {
    color: black;
    font-size: 20px;
    width: 50%;
    padding: 10px;
    margin-bottom: 5px;
    border: 3px #EC008C solid;
    border-radius: 10px;
}
.wordbook-info input::-webkit-input-placeholder {
    color: #EC008C;
    font-size: 20px;
}
.wordbook-info input::-ms-input-placeholder {
    color: #EC008C;
    font-size: 20px;
}
.wordbook-info input::placeholder {
    color: #EC008C;
    font-size: 20px;
}
